import React from 'react';

const date = new Date();
const hour = date.getHours();
let greet;

console.log(hour);

if(hour < 12){
    greet = 'Good Morning';
}else if(hour >= 12 && hour < 17){
    greet = 'Good Afternoon';
}else if(hour >= 17 && hour < 20){
    greet = 'Good Evening';
}else{
    greet = 'Good Night';
}


const Greet = () =>{
    return(
        <>
            {greet}
        </>
    );
}

export default Greet;