import React from 'react';
let todayIs;

const Day = (props) =>{
    switch (props.dayCount) {
        case 0:
            todayIs = 'Sunday';
            break;
        case 1:
            todayIs = 'Monday';
            break;
        case 2:
            todayIs = 'Tuesday';
            break;
        case 3:
            todayIs = 'Wednesday';
            break;
        case 4:
            todayIs = 'Thrusday';
            break;
        case 5:
            todayIs = 'Friday';
            break;
        case 6:
            todayIs = 'Saturday';
            break;
        default:
            todayIs = 'something went wrong!';
            break;
    }
    return (
        <>
            it's <span className = 'todayIs'>{todayIs}</span> today!
        </>
    );
}

export default Day;